import { IClientState } from "../context/ClientContext";

export default async (
    client: IClientState,
    contractAddress: string,
    viewing_key: string,
    keys: string[]
) => {
    try {
        let queryMsg = { get_user_bets: { user_address: client.accountData.address, viewing_key, keys } };
        const response = await client.execute.queryContractSmart(contractAddress, queryMsg);
        return JSON.parse(atob(response)).get_user_bets
    } catch (e){
        if(e.message.includes("User+VK not valid!")){
            localStorage.clear();
        }
        return {
            user_bets: []
        }
    }
}

export interface UserBets {
    user_bets: UserBet[]
}

export interface UserBet {
    round_number: number,
    tier: number,
    number: number,
    claimed_reward: boolean,
    timestamp: number,
}