import { Dispatch, useState } from "react";
import { Modal } from "react-bootstrap";
import bet from "../api/bet";
import { Round } from "../api/getPaginatedRounds";
import { TierConfig, TierConfigs } from "../api/getTierConfigs";
import { UserBets } from "../api/getUserBets";
import withdrawl from "../api/withdrawl";
import constants from "../constants";
import { IClientState } from "../context/ClientContext";
import { errorNotification, successNotification } from "../utils/notifications";

export default ({
    client,
    viewkey,
    poolNumber,
    round,
    pollConfig,
    currentRoundsUserBets,
    getSSCRTBalance, 
    triggerGetPaginatedUserBets,
    getCurrentRoundsStateTrigger
}: {
    client: IClientState | null,
    viewkey: String | null,
    poolNumber: number,
    round: Round,
    pollConfig: TierConfig,
    currentRoundsUserBets: UserBets | null,
    getSSCRTBalance: Function,
    triggerGetPaginatedUserBets: Function,
    getCurrentRoundsStateTrigger: Function
}) => {
    const [buttonLoading, setButtonLoading] = useState<{ pool: number, round: number } | null>(null)
    const [enterPoolModal, setEnterPoolModal] = useState<IEnterPoolModal>({
        show: false,
        data: null
    })
    return (
        <div className="col round-container">
            <div className="row">
                <div className="col" style={{ fontSize: "1.25rem", marginBottom: "5px" }}>
                    Round {round.round_number} -
                    {
                        round.round_end_timestamp && round.round_end_pool_size ?
                            <span style={{ marginLeft: "5px" }} className="badge badge-secondary">Finished</span> :
                            <span style={{ marginLeft: "5px" }} className="badge badge-success">Open</span>
                    }
                </div>
            </div>
            {
                round.round_end_timestamp && round.round_end_pool_size && round.lucky_number &&
                <div className="row" >
                    <div className="col" style={{ fontSize: "1.5rem", marginBottom: "5px" }}>
                        {"Lucky Number: " + round.lucky_number}
                    </div>
                </div>
            }
            <div className="row">
                <div className="col" style={{ marginBottom: "5px" }}>
                    {
                        round.round_end_timestamp && round.round_end_pool_size ?
                            "Triggered at " + new Date(round.round_end_timestamp * 1000).toLocaleString() :
                            poolNumber === 1 ? "Trigger every 12h" :
                                poolNumber === 2 ? "Trigger every 1h" :
                                    poolNumber === 3 && "Trigger every 5m"
                    }
                </div>
            </div>
            <div className="row">
                <div className="col">
                    <span style={{ fontSize: "3rem" }}>
                        {
                            round.round_end_timestamp && round.round_end_pool_size ?
                                `${parseInt(round.round_end_pool_size) / 1000000}` :
                                `${parseInt(round.pool_size) / 1000000}`
                        }
                    </span>
                    <br />
                    {
                        round.round_end_timestamp && round.round_end_pool_size ?
                            "sSCRT Final Pool Size" :
                            "sSCRT Pool Size"
                    }

                </div>
                <div className="col">
                    <div style={{ margin: "5px" }}>{round.users_count}/{pollConfig.min_entries} Participants</div>
                    {
                        round.winner_users_count !== null && <div style={{ margin: "5px" }}>{round.winner_users_count} {round.winner_users_count === 1 ? "Winner" : "Winners"}</div>
                    }
                    {
                        currentRoundsUserBets && currentRoundsUserBets.user_bets.find((bet) => bet.tier === poolNumber && bet.round_number === round.round_number && !bet.claimed_reward && !round.round_end_timestamp) &&
                        <button type="button" className="btn btn-danger" style={{ borderRadius: "10px", margin: "5px" }}
                            onClick={async () => {
                                try {
                                    if (!client || !viewkey) return
                                    setButtonLoading({ pool: poolNumber, round: round.round_number })
                                    await withdrawl(client, constants.LUCKY_NUMBER_CONTRACT_ADDRESS, poolNumber, round.round_number)
                                    await getCurrentRoundsStateTrigger(client, viewkey, true)
                                    await triggerGetPaginatedUserBets()
                                    await getSSCRTBalance()
                                    setButtonLoading(null)
                                    successNotification("Cancel Bet Success!")
                                } catch (e) {
                                    setButtonLoading(null)
                                    errorNotification(e)
                                }
                            }}
                        >
                            {`${parseInt(pollConfig.entry_fee) / 1000000} sSCRT`}
                            <br />
                            {
                                buttonLoading && buttonLoading.pool === poolNumber && buttonLoading.round === round.round_number ?
                                    <i className="fa fa-spinner fa-spin"></i>
                                    : "Cancel Bet"
                            }
                        </button>
                    }
                    {
                        currentRoundsUserBets && currentRoundsUserBets.user_bets.find((bet) => bet.tier === poolNumber && bet.round_number === round.round_number && !bet.claimed_reward && round.round_end_timestamp && bet.number === round.lucky_number) &&
                        <button type="button" className="btn btn-success" style={{ borderRadius: "10px", margin: "5px" }}
                            onClick={async () => {
                                try {
                                    if (!client || !viewkey) return
                                    setButtonLoading({ pool: poolNumber, round: round.round_number })
                                    await withdrawl(client, constants.LUCKY_NUMBER_CONTRACT_ADDRESS, poolNumber, round.round_number)
                                    await getCurrentRoundsStateTrigger(client, viewkey, true)
                                    await getSSCRTBalance()
                                    await triggerGetPaginatedUserBets()
                                    setButtonLoading(null)
                                    successNotification("Withdraw Winnings Success!")
                                } catch (e) {
                                    setButtonLoading(null)
                                    errorNotification(e)
                                }
                            }}
                        >
                            {
                                round.round_end_pool_size && round.winner_users_count ? (parseInt(round.round_end_pool_size) / round.winner_users_count) / 1000000 + " sSCRT" : "..."
                            }
                            <br />
                            {
                                buttonLoading && buttonLoading.pool === poolNumber && buttonLoading.round === round.round_number ?
                                    <i className="fa fa-spinner fa-spin"></i>
                                    : "Claim"
                            }
                        </button>
                    }
                    {
                        currentRoundsUserBets && !currentRoundsUserBets.user_bets.find((bet) => bet.tier === poolNumber && bet.round_number === round.round_number) && !round.lucky_number &&
                        <button type="button" className="btn btn-info" style={{ borderRadius: "10px", margin: "5px" }}
                            onClick={() => setEnterPoolModal({
                                show: true,
                                data: {
                                    pool: {
                                        number: poolNumber,
                                        configs: pollConfig
                                    },
                                    round: round
                                }
                            })}
                        >
                            {`${parseInt(pollConfig.entry_fee) / 1000000} sSCRT`}
                            <br />
                            Enter Pool
                        </button>
                    }
                    {
                        currentRoundsUserBets && currentRoundsUserBets.user_bets.find((bet) => bet.tier === poolNumber && bet.round_number === round.round_number && round.round_end_timestamp && bet.number !== round.lucky_number) &&
                        <button disabled type="button" className="btn btn-danger" style={{ borderRadius: "10px", margin: "5px" }}>
                            You Lost
                        </button>
                    }
                    {
                        currentRoundsUserBets && currentRoundsUserBets.user_bets.find((bet) => bet.tier === poolNumber && bet.round_number === round.round_number && bet.claimed_reward && round.round_end_timestamp) &&
                        <button disabled type="button" className="btn btn-secondary" style={{ borderRadius: "10px", margin: "5px" }}>
                            {
                                round.round_end_pool_size && round.winner_users_count ? (parseInt(round.round_end_pool_size) / round.winner_users_count) / 1000000 + " sSCRT" : "..."
                            }
                            <br />
                            Claimed
                        </button>
                    }
                    {
                        currentRoundsUserBets && !currentRoundsUserBets.user_bets.find((bet) => bet.tier === poolNumber && bet.round_number === round.round_number) && round.lucky_number &&
                        <button type="button" className="btn btn-secondary" style={{ borderRadius: "10px", margin: "5px" }}>
                            Finished
                        </button>
                    }

                </div>
            </div>
            {
                client && viewkey &&
                <EnterPoolModal
                    client={client}
                    viewkey={viewkey}
                    getCurrentRoundsStateTrigger={getCurrentRoundsStateTrigger}
                    getSSCRTBalance={getSSCRTBalance}
                    enterPoolModal={enterPoolModal}
                    setEnterPoolModal={setEnterPoolModal}
                    triggerGetPaginatedUserBets={triggerGetPaginatedUserBets}
                />
            }
        </div>
    )
}


const EnterPoolModal = ({
    client,
    viewkey,
    getCurrentRoundsStateTrigger,
    getSSCRTBalance,
    enterPoolModal,
    setEnterPoolModal,
    triggerGetPaginatedUserBets
}: {
    client: IClientState,
    viewkey: String,
    getCurrentRoundsStateTrigger: Function,
    getSSCRTBalance: Function,
    enterPoolModal: IEnterPoolModal,
    setEnterPoolModal: Dispatch<IEnterPoolModal>,
    triggerGetPaginatedUserBets: Function
}) => {
    const [number, setNumber] = useState<number | null>(null)
    const [betLoading, setBetLoading] = useState(false)

    return (
        <Modal centered show={enterPoolModal.show} onHide={() => setEnterPoolModal({ show: false, data: null })} >
                <div style={{ background: "linear-gradient(180deg, #242525 0%, #000 180%)", color: "white" }}>
                    <div className="modal-header">
                        <h5 className="modal-title">Enter Pool - Choose a Number to Bet</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close" style={{ color: "white" }} onClick={() => setEnterPoolModal({ show: false, data: null })}>
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <div className="row">
                            {
                                enterPoolModal.data && Array.from(Array(enterPoolModal.data.pool.configs.max_rand_number).keys()).map((value, i) =>
                                    <div className="col" style={{textAlign:"center"}}>
                                        <button type="button" style={{ width: "50px", margin: "5px" }} className={`btn btn-${number && number === i + 1 ? "success" : "secondary"}`} onClick={() => setNumber(i + 1)}>
                                            {i + 1}
                                        </button>
                                    </div>
                                )
                            }
                        </div>
                    </div>
                    <div className="modal-footer">
                        <button
                            disabled={number ? false : true}
                            type="button" className="btn btn-success py-2 px-4" data-bs-dismiss="modal" onClick={async () => {
                                try {
                                    if (!enterPoolModal.data || !number) return
                                    setBetLoading(true)
                                    await bet(client, constants.SSCRT_CONTRACT_ADDRESS, constants.LUCKY_NUMBER_CONTRACT_ADDRESS, enterPoolModal.data.pool.number, number, enterPoolModal.data.pool.configs.entry_fee)
                                    await getCurrentRoundsStateTrigger(client, viewkey, true)
                                    await getSSCRTBalance()
                                    await triggerGetPaginatedUserBets()
                                    setBetLoading(false)
                                    setNumber(null)
                                    setEnterPoolModal({ show: false, data: null })
                                    successNotification("Bet Success!")
                                } catch (e) {
                                    setBetLoading(false)
                                    setNumber(null)
                                    errorNotification(e)
                                }
                            }}>
                            {
                                !betLoading ? "Bet" : <i className="fa fa-spinner fa-spin"></i>
                            }
                        </button>
                        <button type="button" className="btn btn-secondary py-2 px-4" data-bs-dismiss="modal" onClick={() => setEnterPoolModal({ show: false, data: null })}>Close</button>
                    </div>
                </div>
        </Modal>
    )
}

interface IEnterPoolModal {
    show: boolean,
    data: {
        pool: {
            number: number,
            configs: TierConfig
        },
        round: Round
    } | null
}