import { IClientState } from "../context/ClientContext";
const { fromUtf8 } = require("@iov/encoding");

export default async (
        client: IClientState, 
        contractAddress: string
    ) => {
    let queryMsg = { get_tier_configs: {tier1: true, tier2: true, tier3: true} };
    const response = await client.execute.queryContractSmart(contractAddress, queryMsg);
    return JSON.parse(atob(response)).get_tier_configs
}

export interface TierConfigs {
    tier1_configs: TierConfig,
    tier2_configs: TierConfig,
    tier3_configs: TierConfig
}

export interface TierConfig  {
    entry_fee: string,
    triggerer_fee: string,
    min_entries: number,
    max_rand_number: number
}