import { IClientState } from "../context/ClientContext";
const { fromUtf8 } = require("@iov/encoding");

export default async (
        client: IClientState, 
        contractAddress: string,
        page: number,
        page_size: number
    ) => {
    let queryMsg = { get_paginated_rounds: {tier1: true, tier2: true, tier3: true, page, page_size} };
    const response = await client.execute.queryContractSmart(contractAddress, queryMsg);
    return JSON.parse(atob(response)).get_paginated_rounds
}

export interface PaginagedRounds {
    tier1_rounds: Round[],
    tier2_rounds: Round[],
    tier3_rounds: Round[]
}

export interface Round  {
    round_number: number,
    pool_size: string,
    lucky_number: number | null,
    users_count: number,
    winner_users_count: number | null,
    round_end_timestamp: number | null,
    round_end_pool_size: string | null
}