import React, { useState, createContext } from "react";
import { SigningCosmWasmClient } from "secretjs";
import { PaginagedRounds } from "../api/getPaginatedRounds";

export const CurrentRoundsStateContext = createContext<PaginagedRounds | null>(null);
export const CurrentRoundsStateDispatchContext = createContext<Function>(() => null);

export default (props: any) => {
    const [CurrentRoundsState, setCurrentRoundsState] = useState<PaginagedRounds | null>(null) 

    return (
        <CurrentRoundsStateContext.Provider value={CurrentRoundsState}>
             <CurrentRoundsStateDispatchContext.Provider value={setCurrentRoundsState}>
                {props.children}
             </CurrentRoundsStateDispatchContext.Provider>
        </CurrentRoundsStateContext.Provider>
    );
  }