import { Dispatch, useEffect, useState } from "react"
import { PaginagedRounds, Round } from "../api/getPaginatedRounds"
import getPaginatedUserBets from "../api/getPaginatedUserBets"
import getRounds from "../api/getRounds"
import { TierConfig, TierConfigs } from "../api/getTierConfigs"
import { UserBet, UserBets } from "../api/getUserBets"
import withdrawl from "../api/withdrawl"
import constants from "../constants"
import { IClientState } from "../context/ClientContext"
import { errorNotification, successNotification } from "../utils/notifications"
import RoundComponent from "./RoundComponent"
import { Col, Dropdown, Form } from "react-bootstrap"

export default ({
    client,
    viewkey,
    currentRoundsState,
    pollConfigs,
    currentRoundsUserBets,
    getSSCRTBalance,
    paginatedUserBets,
    triggerGetPaginatedUserBets,
    getCurrentRoundsStateTrigger,
    paginationValues,
    setPaginationsValues
}: {
    client: IClientState | null,
    viewkey: string | null,
    currentRoundsState: PaginagedRounds | null,
    pollConfigs: TierConfigs | null,
    currentRoundsUserBets: UserBets | null,
    getSSCRTBalance: Function,
    paginatedUserBets: { user_bets: UserBet[], bet_rounds: Round[], user_bets_total_count: number } | null,
    triggerGetPaginatedUserBets: Function,
    getCurrentRoundsStateTrigger: Function,
    paginationValues: {
        page_size: number,
        page: number
    },
    setPaginationsValues: Dispatch<{
        page_size: number,
        page: number
    }>
}) => {
    const [buttonLoading, setButtonLoading] = useState<{ pool: number, round: number } | null>(null)
    const [roundViewer, setRoundViewer] = useState<{ pollNumber: number, round: Round, pollConfig: TierConfig | null } | null>(null)
    const [searchState, setSearchState] = useState<{
        loading: boolean,
        pool: string,
        round: string
    }>({
        loading: false,
        pool: "1",
        round: ""
    })

    useEffect(() => {
        triggerGetPaginatedUserBets();
    }, [client, viewkey])

    const triggerGetRound = async (pollNumber: number, round: number) => {
        if (!client || !pollConfigs ) return
        let tier1_rounds = [];
        let tier2_rounds = [];
        let tier3_rounds = [];

        if (pollNumber === 1) tier1_rounds.push(round)
        if (pollNumber === 2) tier2_rounds.push(round)
        if (pollNumber === 3) tier3_rounds.push(round)

        try{
            const response = await getRounds(client, constants.LUCKY_NUMBER_CONTRACT_ADDRESS, tier1_rounds, tier2_rounds, tier3_rounds)
            const roundState: Round = response.rounds[0];
    
            setRoundViewer({
                pollNumber,
                round: roundState,
                pollConfig: pollNumber === 1 ? pollConfigs.tier1_configs : pollNumber === 2 ? pollConfigs.tier2_configs : pollNumber === 3 ? pollConfigs.tier3_configs : null
            })
        } catch (e) {
            errorNotification(e)
        }
    }

    const handlePageChange = async (page: number) => {
        triggerGetPaginatedUserBets(page - 1, paginationValues.page_size)
        setPaginationsValues({
            page,
            page_size: 5
        })
    }


    if (currentRoundsState && pollConfigs && paginatedUserBets) {
        return (
            <div style={{ color: "white", width: "100%" }}>

                <div className="row" style={{ margin: "0px" }} >
                    <div className="col-8">
                        <div className="row" style={{ justifyContent: "center", margin: "0px" }}>
                            <h2>Bets</h2>
                        </div>
                        <table className="table table-striped table-dark" style={{ margin: "20px" }}>
                            <thead>
                                <tr>
                                    <th scope="col">Date</th>
                                    <th scope="col">Pool</th>
                                    <th scope="col">Round</th>
                                    <th scope="col">Lucky Number</th>
                                    <th scope="col">My Number</th>
                                    <th scope="col">Entry</th>
                                    <th scope="col">Reward</th>
                                    <th scope="col">Status</th>
                                    <th scope="col">Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {

                                }
                                {paginatedUserBets.user_bets.map((bet, i) => {
                                    return (
                                        <tr key={i}>
                                            <td style={{ display: "table-cell", verticalAlign: "middle" }}>
                                                {new Date(bet.timestamp * 1000).toLocaleDateString()}
                                                <br />
                                                {new Date(bet.timestamp * 1000).toLocaleTimeString()}
                                            </td>
                                            <td style={{ display: "table-cell", verticalAlign: "middle" }}>{bet.tier}</td>
                                            <td style={{ display: "table-cell", verticalAlign: "middle" }}>{bet.round_number}</td>
                                            <td style={{ display: "table-cell", verticalAlign: "middle" }}>{
                                                paginatedUserBets.bet_rounds[i].lucky_number !== null ? paginatedUserBets.bet_rounds[i].lucky_number : " - "
                                            }</td>
                                            <td style={{ display: "table-cell", verticalAlign: "middle" }}>{bet.number}</td>
                                            <td style={{ display: "table-cell", verticalAlign: "middle" }}>
                                                {
                                                    bet.tier === 1 ? parseInt(pollConfigs.tier1_configs.entry_fee) / 1000000 + " sSCRT" :
                                                        bet.tier === 2 ? parseInt(pollConfigs.tier2_configs.entry_fee) / 1000000 + " sSCRT" :
                                                            bet.tier === 3 ? parseInt(pollConfigs.tier3_configs.entry_fee) / 1000000 + " sSCRT" :
                                                                "-"
                                                }
                                            </td>
                                            <td style={{ display: "table-cell", verticalAlign: "middle" }}>
                                                {
                                                    paginatedUserBets.bet_rounds[i].lucky_number !== null &&
                                                        bet.number === paginatedUserBets.bet_rounds[i].lucky_number &&
                                                        paginatedUserBets.bet_rounds[i].round_end_pool_size &&
                                                        paginatedUserBets.bet_rounds[i].winner_users_count !== null ?
                                                        (parseInt(paginatedUserBets.bet_rounds[i].round_end_pool_size!) / paginatedUserBets.bet_rounds[i].winner_users_count!) / 1000000 + " sSCRT" : " - "
                                                }
                                            </td>
                                            <td style={{ display: "table-cell", verticalAlign: "middle" }}>
                                                {paginatedUserBets.bet_rounds[i].lucky_number === null ? "Pending" : bet.number === paginatedUserBets.bet_rounds[i].lucky_number ? "Winner" : "Loser"}
                                            </td>
                                            <td style={{ display: "table-cell", verticalAlign: "middle" }}>
                                                {
                                                    paginatedUserBets.bet_rounds[i].lucky_number !== null &&
                                                    bet.number === paginatedUserBets.bet_rounds[i].lucky_number &&
                                                    bet.claimed_reward === false &&
                                                    <button type="button" className="btn btn-success" style={{ borderRadius: "10px", margin: "5px" }}
                                                        disabled={(buttonLoading && buttonLoading.pool === bet.tier && buttonLoading.round === paginatedUserBets.bet_rounds[i].round_number) ? true : false}
                                                        onClick={async () => {
                                                            try {
                                                                if (!client) return
                                                                setButtonLoading({ pool: bet.tier, round: paginatedUserBets.bet_rounds[i].round_number })
                                                                await withdrawl(client, constants.LUCKY_NUMBER_CONTRACT_ADDRESS, bet.tier, paginatedUserBets.bet_rounds[i].round_number)
                                                                await triggerGetPaginatedUserBets()
                                                                await getSSCRTBalance()
                                                                setButtonLoading(null)
                                                                successNotification("Withdraw Winnings Success!")
                                                            } catch (e) {
                                                                setButtonLoading(null)
                                                                errorNotification(e)
                                                            }
                                                        }}>
                                                        {
                                                            buttonLoading && buttonLoading.pool === bet.tier && buttonLoading.round === paginatedUserBets.bet_rounds[i].round_number ?
                                                                <i className="fa fa-spinner fa-spin"></i>
                                                                : "Claim"
                                                        }
                                                    </button>
                                                }
                                                {
                                                    paginatedUserBets.bet_rounds[i].lucky_number !== null &&
                                                    bet.number === paginatedUserBets.bet_rounds[i].lucky_number &&
                                                    bet.claimed_reward === true &&
                                                    <button disabled type="button" className="btn btn-secondary" style={{ borderRadius: "10px", margin: "5px" }}>
                                                        Claimed
                                                    </button>
                                                }

                                                {
                                                    paginatedUserBets.bet_rounds[i].lucky_number !== null &&
                                                    <button
                                                        type="button"
                                                        className="btn btn-secondary"
                                                        style={{ borderRadius: "10px", margin: "5px" }}
                                                        disabled={
                                                            roundViewer &&
                                                                (roundViewer.pollNumber === bet.tier && roundViewer.round.round_number === paginatedUserBets.bet_rounds[i].round_number) ? true : false
                                                        }
                                                        onClick={() => setRoundViewer({
                                                            round: paginatedUserBets.bet_rounds[i],
                                                            pollConfig:
                                                                bet.tier === 1 ? pollConfigs.tier1_configs :
                                                                    bet.tier === 2 ? pollConfigs.tier2_configs :
                                                                        bet.tier === 3 ? pollConfigs.tier3_configs :
                                                                            null,
                                                            pollNumber: bet.tier
                                                        })}
                                                    > <i className="fas fa-eye"></i> </button>
                                                }
                                            </td>
                                        </tr>
                                    )
                                }

                                )}
                            </tbody>
                        </table>
                        <div className="d-flex align-items-center">
                            <div className="ml-auto mr-3">
                                {paginatedUserBets.user_bets_total_count > 0 && "Total number of bets: " + paginatedUserBets.user_bets_total_count}
                            </div>
                            {
                                paginatedUserBets.user_bets_total_count > 0 &&
                                <nav aria-label="...">
                                    <ul className="pagination mb-0 my-3">
                                        <button className="page-item btn btn-secondary rounded-0"
                                            disabled={paginationValues.page === 1}
                                            onClick={() => handlePageChange(1)}>
                                            <i className="fas fa-angle-double-left"></i>
                                        </button>
                                        <button className="page-item btn btn-secondary rounded-0" onClick={() => handlePageChange(paginationValues.page - 1 > 0 ? paginationValues.page - 1 : 1)}>
                                            <i className="fas fa-angle-left"></i>
                                        </button>
                                        <button
                                            className="page-item btn btn-secondary rounded-0"
                                            onClick={() =>
                                                handlePageChange(paginationValues.page + 1 < Math.ceil(paginatedUserBets.user_bets_total_count / paginationValues.page_size) ? paginationValues.page + 1 : Math.ceil(paginatedUserBets.user_bets_total_count / paginationValues.page_size))
                                            }>
                                            <i className="fas fa-angle-right"></i>
                                        </button>
                                        <button className="page-item btn btn-secondary rounded-0"
                                            disabled={paginationValues.page === Math.ceil(paginatedUserBets.user_bets_total_count / paginationValues.page_size)}
                                            onClick={() => handlePageChange(Math.ceil(paginatedUserBets.user_bets_total_count / paginationValues.page_size))}>
                                            <i className="fas fa-angle-double-right"></i>
                                        </button>
                                    </ul>
                                </nav>
                            }
                        </div>
                    </div>
                    <div className="col" style={{ justifyContent: "center", marginLeft:"50px" }}>
                        <div className="row" style={{ justifyContent: "center", margin: "0px" }}>
                            <h2>Search previous Pool/Round</h2>
                        </div>
                        {
                            /*
                            <div className="row" style={{ justifyContent: "center", margin: "20px" }}>
                            Pool
                            <Dropdown style={{margin:"10px"}}>
                                <Dropdown.Toggle variant="secondary" id="dropdown-basic">
                                    {searchState.pool}
                                </Dropdown.Toggle>
                                <Dropdown.Menu>
                                    <Dropdown.Item onClick={() => setSearchState({...searchState, pool: "1"})}>1</Dropdown.Item>
                                    <Dropdown.Item onClick={() => setSearchState({...searchState, pool: "2"})}>2</Dropdown.Item>
                                    <Dropdown.Item onClick={() => setSearchState({...searchState, pool: "3"})}>3</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>

                            Round: <input type="number"/>
                            <button type="button" className="btn btn-secondary"> <i className="fas fa-search"></i> </button>
                        </div>
                            */
                        }

                        {
                            roundViewer && roundViewer.pollConfig &&
                            <div>

                                <div className="row" style={{ justifyContent: "center", margin: "10px" }}>
                                    <h3>{"Pool " + roundViewer.pollNumber + " - Round " + roundViewer.round.round_number}</h3>
                                </div>
                                <div className="row" style={{ justifyContent: "center" }}>
                                    <RoundComponent
                                        client={client}
                                        viewkey={viewkey}
                                        poolNumber={1}
                                        round={roundViewer.round}
                                        pollConfig={roundViewer.pollConfig}
                                        currentRoundsUserBets={currentRoundsUserBets}
                                        getSSCRTBalance={getSSCRTBalance}
                                        getCurrentRoundsStateTrigger={getCurrentRoundsStateTrigger}
                                        triggerGetPaginatedUserBets={triggerGetPaginatedUserBets}
                                    />
                                </div>

                            </div>
                        }
                        <Form style={{ margin: "20px" }}>
                            <Form.Row>
                                <Form.Group as={Col} style={{textAlign: "left", width: "80%"}}>
                                    <Form.Label>Pool</Form.Label>
                                    <Form.Control as="select" defaultValue="-" style={{textAlignLast: "center"}}
                                    value={searchState.pool}
                                    onChange={(e) => setSearchState({
                                        ...searchState,
                                        pool: e.target.value
                                    })}
                                    >
                                        <option value={1}>1</option>
                                        <option value={2}>2</option>
                                        <option value={3}>3</option>
                                    </Form.Control>
                                </Form.Group>

                                <Form.Group as={Col} style={{textAlign: "left"}}>
                                    <Form.Label>Round</Form.Label>
                                    <Form.Control type="number" placeholder="Round" min={0} style={{textAlign: "center"}}
                                        value={searchState.round}
                                        onChange={(e) => setSearchState({
                                            ...searchState,
                                            round: e.target.value
                                        })}
                                    />
                                </Form.Group>

                                <Form.Group as={Col} style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
                                    <button type="button" style={{width: "70%", height: "70%"}}
                                        disabled={
                                            searchState.round === "" || 
                                            searchState.pool === "1" && parseInt(searchState.round) >= currentRoundsState.tier1_rounds[0].round_number ||
                                            searchState.pool === "2" && parseInt(searchState.round) >= currentRoundsState.tier2_rounds[0].round_number ||
                                            searchState.pool === "3" && parseInt(searchState.round) >= currentRoundsState.tier3_rounds[0].round_number
                                        }
                                        onClick={() => {
                                            if (!client) return
                                            triggerGetRound(
                                                parseInt(searchState.pool),
                                                parseInt(searchState.round) 
                                                )
                                        }}
                                        className="btn btn-info"><i className="fas fa-search"></i></button>
                                </Form.Group>
                            </Form.Row>

                        </Form>
                    </div>
                </div>
            </div>
        )
    } else {
        return null
    }
}