import { IClientState } from "../context/ClientContext";
const { fromUtf8 } = require("@iov/encoding");

export default async (
    client: IClientState,
    contractAddress: string,
    viewing_key: string,
    page: number,
    page_size: number
) => {
    try {
        let queryMsg = { get_paginated_user_bets: { user_address: client.accountData.address, viewing_key, page, page_size } };
        const response = await client.execute.queryContractSmart(contractAddress, queryMsg);
        return JSON.parse(atob(response)).get_paginated_user_bets
    } catch (e) {
        if (e.message.includes("User+VK not valid!")) {
            localStorage.clear();
        }
        return {
            user_bets: [],
            bet_rounds: []
        }
    }
}